import {BaseValidator} from "./BaseValidator";

export class LoginValidator extends BaseValidator {
  
  constructor() {
    
    const validFncs: {text: string, fn: (value: string) => boolean}[] = [];
  
    validFncs.push({
      text: " ",
      fn: (value: string) => {
        return /\w+/.test(value);
      }
    });
    
    validFncs.push({
      text: " 3  20 ",
      fn: (value: string) => {
        return value.length >= 3 && value.length <= 20;
      }
    });
  
    validFncs.push({
      text: " ",
      fn: (value: string) => {
        return !/[--]/.test(value);
      }
    });
  
    validFncs.push({
      text: "   ",
      fn: (value: string) => {
        return !/["'&<>\s]/.test(value);
      }
    });

    
    super(validFncs);
  }
  
}
